<template>
    <v-container fluid>
        <div v-for="(item,index) in articles" :key="index">
            <h1 class="text-center">{{item.dateString}}</h1>
            <v-timeline align-top :dense="$vuetify.breakpoint.smAndDown">
                <v-timeline-item v-for="(article,articleIndex) in item.articles" :key="article.id"
                                 :color="articleIndex%2===0?'purple':'blue'" fill-dot
                                 :right="articleIndex%2===0" :small="articleIndex%2===0">
                    <v-hover>
                        <template v-slot:default="{ hover }">
                            <v-card max-height="200px" :color="articleIndex%2===0?'#82b1ff':'#1976d2'">
                                <v-card-title class="white--text pa-2">
                                    {{article.title}}
                                </v-card-title>
                                <v-card tile flat>
                                    <v-card-text class="pa-2">{{article.abstract}}</v-card-text>
                                </v-card>
                                <v-fade-transition>
                                    <v-overlay v-if="hover" absolute>
                                        <v-btn :color="articleIndex%2===0?'purple':'blue'"
                                               @click="clickArticle(article)">
                                            查看详情
                                        </v-btn>
                                    </v-overlay>
                                </v-fade-transition>
                            </v-card>
                        </template>
                    </v-hover>
                    <template :slot="$vuetify.breakpoint.smAndDown?'default':'opposite'">
                        <v-card-text :class="$vuetify.breakpoint.smAndDown?
                            'pa-1 text--secondary':'pa-1 text--primary'">
                            {{article.timeString}}
                        </v-card-text>
                    </template>
                </v-timeline-item>
            </v-timeline>
            <v-divider v-if="index!==articles.length-1"/>
        </div>
    </v-container>
</template>

<script>
    export default {
        name: "TimeLine",
        data() {
            return {
                articles: [
                    {
                        dateString: "2020年",
                        articles: [
                            {
                                id: 1,
                                title: "标题1",
                                abstract: "内容1",
                                timeString: "05/17 21:36"
                            },
                            {
                                id: 2,
                                title: "标题2",
                                abstract: "内容2",
                                timeString: "05/17 21:36"
                            },
                            {
                                id: 3,
                                title: "标题3",
                                abstract: "内容3",
                                timeString: "05/17 21:36"
                            }
                        ]
                    },
                    {
                        dateString: "2019年",
                        articles: [
                            {
                                id: 4,
                                title: "标题4",
                                abstract: "内容4",
                                timeString: "05/17 21:36"
                            },
                            {
                                id: 5,
                                title: "标题5",
                                abstract: "内容5",
                                timeString: "05/17 21:36"
                            },
                            {
                                id: 6,
                                title: "标题6",
                                abstract: "内容6",
                                timeString: "05/17 21:36"
                            }
                        ]
                    }
                ]
            }
        },
        methods: {
            clickArticle: function (article) {
                //path不能和params连用
                this.$router.push({
                    name: "article",
                    params: {id: article.id, title: article.title}
                })
            }
        }
    }
</script>

